@font-face {
  font-family: 'icomoon';
  src:  url('./fonts/icomoon.eot?bmeejh');
  src:  url('./fonts/icomoon.eot?bmeejh#iefix') format('embedded-opentype'),
    url('./fonts/icomoon.ttf?bmeejh') format('truetype'),
    url('./fonts/icomoon.woff?bmeejh') format('woff'),
    url('./fonts/icomoon.svg?bmeejh#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-search:before {
  content: "\1f32b";
}
.icon-delete:before {
  content: "\e9ac";
}
.icon-eye-blocked:before {
  content: "\e9d1";
}
.icon-checkmark:before {
  content: "\ea10";
}
.icon-github:before {
  content: "\eab0";
}
.icon-linkedin:before {
  content: "\eac9";
}